.container {
  max-width: 1390px;
  margin: auto;
}

.footer {
  margin-top: 80px;
  background-color: #4f66cf;
  color: #ffffff;
}

.footer-row {
  padding-top: 65px;
  display: flex;
  justify-content: space-between;
}

.footer-infp-label {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 40px;
  margin-bottom: 30px;
}

.phone-number {
  font-family: "Rubik";
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
  text-decoration: none;
  color: #4f66cf;
  background-color: #ffffff;
  padding: 15px 40px;
  border-radius: 24px;
  transition-duration: 0.3s;
}

.phone-number:hover {
  background-color: #8b9df0;
  color: #ffffff;
}

.footer-info-text {
  margin-top: 34px;
}

.footer-text {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
}

.footer-socials {
  max-width: 690px;
  margin-bottom: 240px;
}

.footer-socials-text {
  margin-left: 15px;
  margin-bottom: 30px;
}

.footer-socials-list {
  display: flex;
  flex-wrap: wrap;
}
.footer-socials-list li {
  margin: 12px;
  width: 320px;
  background-color: #7085e4;
  border-radius: 24px;
}
.footer-socials-list li a {
  padding: 15px 24px;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #ffffff;
}
.footer-socials-list li a img {
  margin-right: 15px;
}
.footer-socials-list li:hover {
  transition-duration: 0.3s;
  background-color: #8b9df0;
}

/************************************/
@media (max-width: 425px) {
  .footer-row {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .footer-info-text {
    display: none;
  }

  .footer-info {
    text-align: center;
  }
  .footer-infp-label {
    margin-bottom: 50px;
  }
  .footer-infp-label span {
    display: none;
  }

  .footer-socials {
    margin-top: 50px;
    text-align: center;
  }

  .footer-socials-list {
    justify-content: center;
  }
  .footer-socials-list li {
    width: 300px;
    font-size: 24px;
    line-height: 32px;
  }

  .footer-socials-text {
    margin-bottom: 15px;
  }
}
/************************************/
.mobile-call {
  display: none;
}

@media (max-width: 425px) {
  .mobile-call {
    display: block;
    z-index: 5;
    background-color: #5bd262;
    border-radius: 50%;
    position: fixed;
    padding: 25px;
    top: 80%;
    left: 70%;
    border: 4px solid #78e87e;
  }
  .mobile-call img {
    height: 30px;
    width: 30px;
  }

  .container {
    padding: 0px;
  }
}