.container {
  padding: 0px 15px;
}

.content {
  max-width: 900px;
  margin: auto;
}

.swiper {
  margin-top: 40px;
  height: 520px;
  width: 900px;
}

.swiper-slide {
  border-radius: 24px;
}
.swiper-slide img {
  border-radius: 24px;
  height: 100%;
  width: 100%;
}

.swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9;
}

.swiper-pagination-bullet {
  display: inline-block;
  border-radius: 50%;
  background: var(--swiper-pagination-bullet-inactive-color, #a1b1c2);
  opacity: var(--swiper-pagination-bullet-inactive-opacity, 1);
}

.swiper-pagination-bullet-active {
  background: var(--swiper-pagination-bullet-active-inactive-color, #ffffff);
  width: var(--swiper-pagination-bullet-active-width, var(--swiper-pagination-bullet-size, 12px));
  height: var(--swiper-pagination-bullet-active-height, var(--swiper-pagination-bullet-size, 12px));
}

.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 45px;
  left: 0;
  width: 100%;
}

.swiper-button-prev,
.swiper-button-next {
  background-image: url(../../public/img/icons/Back.png);
  background-repeat: no-repeat;
  position: absolute;
  top: 90%;
  left: 35px;
  width: 50px;
  height: 50px;
  display: block;
}

.swiper-button-next {
  background-image: url(../../public/img/icons/Next.png);
  left: 100px;
}

.swiper-button-next::after {
  content: " ";
}

.swiper-button-prev::after {
  content: " ";
}

.label {
  padding-top: 30px;
  margin-bottom: 12px;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
}

.min-order {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 24px;
}
.min-order span {
  color: #4f66cf;
}

.tegs {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 70px;
  flex-wrap: wrap;
}

.tag {
  text-decoration: none;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  padding: 8px 15px;
  margin: 4px;
  border-radius: 47px;
}
.child-partyPage {
  background-color: #ecefff;
  color: #4f66cf;
  transition-duration: 0.3s;
}

.child-partyPage:hover {
  background-color: #4f66cf;
  color: #ffffff;
}

.city-dayPage {
  background-color: #e3f5e4;
  color: #1a9c22;
  transition-duration: 0.3s;
}

.city-dayPage:hover {
  background-color: #5bd262;
  color: #ffffff;
}

.corporatePage {
  background-color: #fcf5ee;
  color: #e7881f;
  transition-duration: 0.3s;
}

.corporatePage:hover {
  background-color: #fc992c;
  color: #ffffff;
}

.festivalPage {
  background-color: #f9e9f0;
  color: #e04685;
  transition-duration: 0.3s;
}

.festivalPage:hover {
  background-color: #f97caf;
  color: #ffffff;
}

.promotionPage {
  background-color: #fcf4e1;
  color: #ac862e;
  transition-duration: 0.3s;
}

.promotionPage:hover {
  background-color: #fadc96;
  color: black;
}

.birthdayPage {
  background-color: #ede7f8;
  color: #7d48db;
  transition-duration: 0.3s;
}

.birthdayPage:hover {
  background-color: #7d48db;
  color: #ffffff;
}

.description {
  position: relative;
}

.description::after {
  content: "";
  position: absolute;
  left: 0;
  top: -30px;
  height: 1px;
  width: 90%;
  background-color: #e0e2eb;
}

.decription-title {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #6b708a;
  margin-bottom: 10px;
}

.text {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  margin-bottom: 50px;
}

.complactation p {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 24px;
  margin-bottom: 25px;
}

.complact-list {
  margin-bottom: 65px;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
}
.complact-list li::before {
  content: "";
  background: url(../img/icons/rhomb.svg);
  background-size: cover;
  width: 16px;
  height: 16px;
  display: inline-block;
  margin-right: 15px;
}

.important {
  background-color: #f8e5ed;
  border-radius: 24px;
  width: 900px;
  max-height: 300px;
  min-height: 130px;
}

.important-label {
  background-color: #f97caf;
  display: inline-block;
  padding: 4px 8px;
  border-radius: 5px;
  margin: 25px 25px;
  color: #ffffff;
}

.important-text {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  margin-left: 25px;
}

.quantity {
  margin-top: 65px;
  display: flex;
  margin-bottom: 180px;
  flex-wrap: wrap;
}
.quantity :nth-child(0) {
  margin: 0px 60px;
}

.quantity-card {
  display: flex;
  align-items: center;
  margin: 12.5px 0px;
  margin-right: 120px;
}

.players p {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
}

.player {
  background-color: #fc992c;
}

.trampoline {
  margin-left: 0px;
  display: flex;
  align-items: center;
}
.trampoline p {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
}

.jump {
  background-color: #7d48db;
}

.number {
  border-radius: 50%;
  padding: 12px 20px;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  text-align: center;
  color: #ffffff;
  margin-right: 15px;
}

@media (max-width: 425px) {
  .container {
    max-width: 415px;
    margin: auto;
    padding: 0px;
  }

  .page-text-part {
    padding: 0px 5px;
    z-index: 5;
    position: relative;
    background-color: #ffffff;
    top: -24px;
    border-radius: 24px 24px 0px 0px;
  }

  .important {
    padding-bottom: 25px;
    width: 320px;
  }

  .important-label {
    margin-bottom: 10px;
  }

  .quantity {
    max-width: 320px;
  }

  .swiper {
    margin-top: 0px;
    border-radius: 0px;
    left: -5px;
    width: 100vw;
  }

  .swiper-slide img {
    border-radius: 0px;
  }
}